<template>
  <div :class="{ gold: rank === 1, silver: rank === 2, bronze: rank === 3, top3: rank < 4 }" class="ranking-cell" @click="toggleStatsDisplay">
    <div v-if="player.isInMatch" class="is-playing blink"/>
    <span class="rank">{{ bakedRank }}</span>
    <img :src="player.photoURL" class="photo">
    <div class="info">
      <span v-if="rank < 4 && player.title" class="title shimmer">{{player.title}}</span>
      <span class="name">{{player.fullname}}</span>
      <span v-if="rank < 4 && player.bio" class="bio">{{player.bio}}</span>
    </div>
    <div class="stats">
      <span class="points">{{player.points}}</span>
      <span class="details">{{showWinRate ? winRate : winLose}}</span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'RankingCell',
  props: {
    rank: {
      type: Number,
      default: 0,
    },
    player: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      stats: undefined,
      showWinRate: true,
    }
  },
  computed: {
    bakedRank() {
      if (this.rank === 1) {
        return '🏅'
      }
      if (this.rank === 2) {
        return '🥈'
      }
      if (this.rank === 3) {
        return '🥉'
      }
      return this.rank
    },
    winRate() {
      if (!this.stats) return ''
      const { win, lose } = this.stats
      if (win === 0 && lose === 0) {
        return ''
      }
      return `${Math.ceil(win / (win + lose) * 100)}%`
    },
    winLose() {
      if (!this.stats) return ''
      const { win, lose } = this.stats
      if (win === 0 && lose === 0) {
        return ''
      }
      return `${win} | ${lose}`
    },
  },
  created() {
    this.$fns
      .httpsCallable('getUserStatistics')({
        uid: this.player.id,
      })
      .then(({ data }) => {
        this.stats = data
      })
  },
  methods: {
    toggleStatsDisplay() {
      this.showWinRate = !this.showWinRate
    },
  },
}
</script>
