const FM = (() => {
  const messages = []
  let lastMessage = undefined
  let intervalRef = undefined

  return {
    add: (msg) => {
      messages.push(msg)
    },
    start: (update, interval = 500, defaultMessage) => {
      intervalRef = setInterval(() => {
        if (messages.length > 0 && lastMessage === messages[0]) {
          messages.shift()
        }
        lastMessage = messages.length > 0 ? messages[0] : defaultMessage
        update(lastMessage)
      }, interval)
      window.i = intervalRef
    },
    stop: () => {
      intervalRef && clearInterval(intervalRef)
    },
  }
})()

export default FM
